Vue.use(Vuex)

export default new Vuex.Store({
  state: {
    blogs: [
      {
        id: 'b1',
        title: 'Front End Developer Journey',
        summary: 'Some Text Here',
        text: 'Full Text on my thought'
      },
      {
        id: 'b2',
        title: 'My take on learning html',
        summary: 'Some Text Here',
        text: 'Full Text on my thought'
      },
      {
        id: 'b3',
        title: 'My take on learning css',
        summary: 'Some Text Here',
        text: 'Full Text on my thought'
      },
      {
        id: 'b4',
        title: 'My take on learning javascript',
        summary: 'Some Text Here',
        text: 'Full Text on my thought'
      },
      {
        id: 'b5',
        title: 'My take on getting over the hump',
        summary: 'Some Text Here',
        text: 'Full Text on my thought'
      },
      {
        id: 'b6',
        title: 'My take on front end framework',
        summary: 'Some Text Here',
        text: 'Full Text on my thought'
      },
      {
        id: 'b7',
        title: 'My take on Vuejs',
        summary: 'Some Text Here',
        text: 'Full Text on my thought'
      }
    ],
    blog: []
  },
  mutations: {
    SET_BLOGS(state, blogs) {
      state.blogs = blogs
    },
    SET_BLOG(state, blog) {
      state.blog = blog
    }
  },
  actions: {
    fetchBlog({ commit, getters }, id) {
      var blog = getters.getBlog(id)
      if (blog) {
        commit('SET_BLOG', blog)
        return blog
      }
    }
  },
  getters: {
    getBlog: state => id => {
      return state.blogs.find(blog => blog.id === id)
    }
  }
})
