<template>
  <div>
    <SingleDialogue :id="this.currentID" :blog="blog" />
  </div>
</template>

<script>
import SingleDialogue from '@/components/SingleDialogue'
import { mapState } from 'vuex'
export default {
  components: {
    SingleDialogue
  },
  props: ['id'],
  data() {
    return {
      currentID: this.id
    }
  },
  computed: mapState(['blog'])
}
</script>

