<template>
  <div>
    <div class="nav">
      <div class="dash" @click="showProfile">
        <i class="fas fa-bars"></i>
      </div>
      <router-link :to="{ name: 'Home' }">Blog</router-link>
    </div>
    <div class="profile" :style="{ marginLeft: marginLeft }">
      <img class="picture" />
      <div class="name">Kimleang Nao</div>
    </div>
  </div>
</template>

<script>
export default {
  data() {
    return {
      marginLeft: null,
      marginActive: false
    }
  },
  methods: {
    showProfile() {
      if (!this.marginActive) {
        this.marginLeft = 0 + 'em'
        this.marginActive = true
      } else {
        this.marginLeft = -20 + 'em'
        this.marginActive = false
      }
    }
  }
}
</script>

<style lang="scss" scoped>
.profile {
  position: fixed;
  margin-top: 4em;
  width: 20em;
  height: 100%;
  margin-left: -20em;
  background-color: white;
  transition: margin-left 1s;
  .picture {
    width: 100%;
    height: 20em;
    background-color: lightgrey;
  }
  .name {
    width: 100%;
    height: 2em;
    background-color: lightpink;
    line-height: 2em;
    font-size: 1.5em;
  }
}
.nav {
  position: fixed;
  width: 100%;
  height: 4em;
  background-color: rgb(39, 37, 38);
  color: white;
  .dash {
    float: left;
    margin-left: 0.2em;
    font-size: 2em;
    line-height: 2em;
    cursor: pointer;
  }
  a {
    line-height: 2em;
    text-decoration: none;
    color: white;
    font-size: 2em;
  }
}
</style>
